import Vue from 'vue';
import Component from 'vue-class-component';
import { IUserInterface } from '~/entity/user';
import { IMatchmakerEvalutionParams } from '~/entity/route_params';
import Utils from '~/utils';
import { Action, State } from 'vuex-class';

import Api from '~/api';
import { IlocalPage, Ipage } from '~/entity/page';
import { LOADING_TEXT, ROOT_PATH, sharedesc1 } from '~/config';
import { ToastMsg } from '~/entity/toast';

interface IMatchMakerEvalution {
    comment: string; // 评价
    createTime: string; // 评论时间
    grade: number; // 评分
    headPortrait: string; // 评论者头像
    realname: string; // 评论者姓名
    sender: string; // 评论者uid

    stars: boolean[]; // 星星评分数组，自己添加 
}

@Component({})
export default class MatchmakerEvalutionComp extends Vue { 

    /********** 分页 列表 ************ */
    pageSize = 10;

    refreshingEvalution = false;

    loadingEvalution = false;

    evalutionPage: IlocalPage = {
        hasMore: false,
        curPage: 1
    };

    loadingText = LOADING_TEXT;

    /********** 用户信息 ********* */
    userInfo: IUserInterface = null;

    evalutions: IMatchMakerEvalution[] = null;

    params: IMatchmakerEvalutionParams = null;

    // 获取评价总次数
    evalutionCount: number = 0;

    // 平均评论次数
    avgGrade: number = 0;

    evalutionsContainer: HTMLElement = null;

    @State(state => state.user.user.uid)
    myUid: string;

    @Action('showToast')
    showToast;

    $refs: {
        evalutionsContainer: HTMLElement;
    };

    get stars() {
        // this.userInfo.matchmakerInfo
        return Utils.getStarsGrade(this.avgGrade);
    }

    mounted() {
        this.evalutionsContainer = this.$refs.evalutionsContainer;
    }

    async activated() {
        await Utils.initedUser();
        const params: IMatchmakerEvalutionParams = this['$route'].params;
        this.params = params;
        if (this.myUid === params.uid) {
            // 获取自己信息
            this.userInfo = this['$store'].state.user.user;
        } else {
            // 获取他人信息
            await this.getOtherUserInfo();
        }
        // 获取评价信息
        this.getTopTaskComment();
        // 刷新列表
        this.refreshEvalution();

        // 初始化分享
        this.initShare();
    }

    initShare() {
        const url = ROOT_PATH + '/personal/findMatchmaker/' + this.userInfo.uid;
        const imgUrl = Utils.getThumbnailImg(this.userInfo.headPortrait);
        const desc = sharedesc1;
        const title = '[推荐]靠谱红娘' + this.userInfo.realname;
        // Utils.initWX(url, title, desc, imgUrl);
    }

    deactivated() {
        console.log('deactivated');
    }

    /**
     * 获取他人用户信息
     */
    async getOtherUserInfo() {
        const res = await Api.getOtherUserInfo(this.params.uid);
        this.userInfo = res.body.obj;
    }

    // 红娘评分
    async getTopTaskComment() {
        const res = await Api.getTopTaskComment(this.params.uid);
        this.avgGrade = res.body.obj.avgGrade || 0;
    }

    /***** 列表 **** */
    /**
     * 刷新评论列表
     */
    async refreshEvalution() {
        this.refreshingEvalution = true;
        try {
            const res = await Api.getMatchMakerEvalutions(this.params.uid, 1, this.pageSize);
            this.evalutions = res.body.obj.rows;

            // 加工 评分数组
            this.setStarGrade(this.evalutions);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.evalutionPage, page);

            // 设置评分总次数
            this.evalutionCount = page.totalRows;
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.refreshingEvalution = false;
        }
    }

    async loadMoreEvalution() {
        this.loadingEvalution = true;
        try {
            const res = await Api.getMatchMakerEvalutions(this.params.uid, this.evalutionPage.curPage + 1, this.pageSize);
            const evalutions: IMatchMakerEvalution[] = res.body.obj.rows;

            this.setStarGrade(evalutions);
            this.evalutions = this.evalutions.concat(evalutions);

            const page: Ipage = res.body.obj.page;
            Utils.checkPage(this.evalutionPage, page);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        } finally {
            this.loadingEvalution = false;
        }
    }

    /**
     * 根据评价评分 设置 星星数组
     * @param evalutions 
     */
    setStarGrade(evalutions: IMatchMakerEvalution[]) {
        for (const evalution of evalutions) {
            evalution.stars = Utils.getStarsGrade(evalution.grade);
        }
    }
}



